___CSS_LOADER_EXPORT___.push([module.id, `*{
    font-family: Poppins,sans-serif;
}
body{
    background-color: #ffd230;
    margin: 0;
    padding: 0;
    border: 0;
}
a{
    text-decoration: none;
}

#navbar{
    display: flex;
    justify-content: center;
    gap: 3rem;
    margin-top: 10px;
    width: 100vw;
}
#navbar a{
    padding: 20px;
}

/* home page */
.wrapper{
    text-align: center;
}
.wrapper img{
    border-radius: 10px;
}
.home-wrapper h1{
    margin-top: -5px;
    margin-bottom: 0px;
    font-size: 72px;
}
.home-wrapper p{
    margin-top: -10px;
    margin-bottom: 30px;
}

/* recipe page */
.recipe-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.recipe-wrapper h1{
    margin-bottom: 10px;
    font-size: 56px;
    margin-top: 0px;
}
.cards-wrapper{
    display: flex;
}
.card{
    width: 300px;
    padding: 5px;
}
.card p{
    margin-top: -15px;
}

/* contact page */
.contact-wrapper h1{
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 56px;
}
.contact-wrapper h3{
    margin-bottom: 0px;
}
.contact-wrapper p{
    margin-top: 5px;
